import React, { useState, useEffect } from "react";
import DropdownForm2 from "./DropdownForm2.js";
import RequestStatus from "./RequestStatus.js";

function TokenFile() {
  const [clientToken, setclientToken] = useState("");
  const [clientDomain, setclientDomain] = useState("");
  const [auth, setAuth] = useState(false);
  const [formTypes, setformTypes] = useState([]);
  const [reqId, setReqId] = useState("");

  function setKeyValue(key, value) {
    localStorage.setItem(key, value);
  }
  function setOfficeKeyValue(key, value) {
    Office.context.roamingSettings.set(key, value);
    Office.context.roamingSettings.saveAsync(); // Save changes asynchronously
  }
  // Retrieve data from localStorage
  function getKeyValue(key) {
    return localStorage.getItem(key);
  }
  function getOfficeKeyValue(key) {
    return Office.context.roamingSettings.get(key);
  }
  useEffect(() => {
    console.log("office context is", Office.context);
    const conversation = Office.context.mailbox.initialData.conversationId;
    const requestId = getOfficeKeyValue(conversation);
    // const requestId=getOfficeKeyValue(conversation)
    if (requestId) {
      setReqId(requestId);
    }
    const domain = getOfficeKeyValue("clientDomain");
    const token = getOfficeKeyValue("clientToken");
    // const domain= getOfficeKeyValue("clientDomain")
    // const token= getOfficeKeyValue("clientToken")

    if (domain && token) {
      setAuth(true);
      const orgId = token.split(".").pop();
      fetchAndSaveStreamlineForms(domain, orgId);

      setclientToken(token);
      setclientDomain(domain);
    }
  }, []);

  async function saveStreamlineSettings(e) {
    e.preventDefault();

    // console.log("renterrrr",Office.context.mailbox)
    // Office.context.document.settings.set('clientToken', clientToken);
    // Office.context.roamingSettings.set("clientToken", clientToken);
    //   var value1 = Office.context.roamingSettings.get("clientToken");
    //  console.log("value",value1)
    if (!clientToken || !clientDomain) {
      return console.log("Failed: Client token and domain both are required");
    }

    // if(!domainPattern.test(clientDomain)){
    //    return console.log("Invalid streamline domain, please provide a valid streamline domain");
    // }

    // console.log("domain name",clientDomain,Office.context.mailbox.initialData.userEmailAddress)
    const options = {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },

      body: JSON.stringify({
        clientToken,
        clientDomain,
        email: "streamline@wlc.technology",
        // email: Office.context.mailbox.initialData.userEmailAddress
      }),
      muteHttpExceptions: true,
    };
    const response = await fetch(`${clientDomain}/api/outlook/validate`, options);

    if (response.status === 400) {
      return console.log("Failed: Invalid Client Token.");
    }

    if (response.status === 401) {
      return console.log("Failed: Please sign in to Streamline using your Google account to continue.");
    }

    if (response.status !== 204) {
      return console.log("Failed: Please check the client token and doamin.");
    }

    if (response.status == 204) {
    }
    setOfficeKeyValue("clientToken", clientToken);
    setOfficeKeyValue("clientDomain", clientDomain);
    // setOfficeKeyValue("clientToken",clientToken)
    // setOfficeKeyValue("clientDomain",clientDomain)
    await fetchAndSaveStreamlineForms(clientDomain, clientToken.split(".").pop());
    setAuth(true);
    Office.context.roamingSettings.set("clientToken", clientToken);
    Office.context.roamingSettings.set("clientDomain", clientDomain);
  }
  // Office.context.mailbox.initialData.userEmailAddress
  async function fetchAndSaveStreamlineForms(clientDomain, orgId) {
    // const token = Office.context.roamingSettings.get("clientToken");
    // const domain =Office.context.roamingSettings.get('clientDomain');
    const response = await fetch(`${clientDomain}/api/outlook/request-forms/${orgId}`);
    const formdata = await response.json();
    setformTypes([...formdata]);
    console.log("fetch response is", formdata);
    if (response.status === 400) {
      return console.log("Failed: Ensure integration is enabled and log in with your Google account in Streamline.");
    }

    // const streamlineRequestForms = JSON.parse(response.body);
    // console.log("streamlineRequestForms",streamlineRequestForms)
    // userProperties.setProperty('streamline-request-forms', JSON.stringify(streamlineRequestForms));
  }
  return (
    <>
      {reqId && auth ? (
        <RequestStatus reqId={reqId} clientToken={clientToken} clientDomain={clientDomain} />
      ) : auth ? (
        <>
          <DropdownForm2 formDetails={formTypes} clientDomain={clientDomain} orgId={clientToken.split(".").pop()} />
        </>
      ) : (
        <>
          <p style={{ height: "25px", fontWeight: "bold", fontFamily: "sans-serif", fontSize: "20px" }}>
            Set Up Streamline Access
          </p>
          <hr style={{ marginTop: "10px" }} />
          <form>
            <label
              style={{
                marginTop: "30px",
                marginLeft: "30px",
                fontSize: "16px",
                fontWeight: "bold",
                fontFamily: "sans-serif",
              }}
            >
              Client Token
            </label>{" "}
            <br />
            <input
              placeholder="Client Token"
              style={{
                marginTop: "6px",
                marginLeft: "30px",
                height: "30px",
                width: "85%",
                border: "1px solid black",
                borderRadius: "5px",
              }}
              onChange={(e) => {
                setclientToken(e.target.value);
              }}
            />{" "}
            <br />
            <div style={{ marginTop: "20px" }}>
              <label style={{ marginLeft: "30px", fontSize: "16px", fontWeight: "bold", fontFamily: "sans-serif" }}>
                Domains Name
              </label>{" "}
              <br />
              <input
                placeholder="Streamline Domain"
                onChange={(e) => {
                  setclientDomain(e.target.value);
                }}
                style={{
                  marginTop: "6px",
                  marginLeft: "30px",
                  height: "30px",
                  width: "85%",
                  border: "1px solid black",
                  borderRadius: "5px",
                }}
              />
            </div>
            <br />
            <button
              style={{
                marginLeft: "30px",
                height: "30px",
                width: "80px",
                marginTop: "3px",
                backgroundColor: "white",
                fontSize: "16px",
                borderRadius: "5px",
              }}
              onClick={(e) => {
                saveStreamlineSettings(e);
              }}
            >
              Save
            </button>
          </form>
        </>
      )}
    </>
  );
}

export default TokenFile;
